import { Component, OnInit } from '@angular/core';
import { MoveDataService, Move } from '../../data/move-data.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-basic-moves',
  templateUrl: './basic-moves.component.html',
  styleUrls: ['./basic-moves.component.scss']
})
export class BasicMovesComponent implements OnInit {

  private basicMoves: Move[];

  constructor(
    private moveDataService: MoveDataService,
    private translateService: TranslateService) { }

  ngOnInit() {
    this.translateService.getTranslation(this.translateService.currentLang).subscribe(e => {
      this.basicMoves = this.moveDataService.findAll(m => {
        return m.tags.find(t => t.name == 'TAG.BASEMOVE') != null;
      }).sort((a,b) => {
        return this.translateService.instant(a.name) < this.translateService.instant(b.name) ? -1 : 1;
      });
    });

    this.translateService.onLangChange.subscribe(e => {
      this.translateService.getTranslation(this.translateService.currentLang).subscribe(e => {
        this.basicMoves = this.basicMoves.sort((a,b) => {
          return this.translateService.instant(a.name) < this.translateService.instant(b.name) ? -1 : 1;
        });
      });
    });
  }

}



