import { Injectable } from '@angular/core';
import { Cachable, ObjectCache } from './ObjectCache';

export interface Tag extends Cachable {
  domain: string;
  name: string;
}

@Injectable()
export class TagDataService extends ObjectCache<Tag>{

  constructor() {
    super();
  }

  findByDomain(domain: string): Tag[] {
    return this.findAll(tag => tag.domain == domain);
  }

  findByName(name: string): Tag[] {
    return this.findAll(tag => tag.name == name);
  }

  findOneByName(name: string): Tag {
    return this.findAll(tag => tag.name == name)[0];
  }

}



