import { Injectable } from '@angular/core';
import { ObjectCache, Cachable } from './ObjectCache';
import { Tag } from './tag-data.service';

export interface Move extends Cachable {
  slug: string;
  name: string;
  trigger?: string;
  effect?: string;
  rollModifier?: string;
  rollResultBreakdown?: RollResultBreakdown[];
  details?:string;
  comments?: string[];
  tags: Tag[]
}

export interface RollResultBreakdown {
  range: string;
  effect: string;
}

@Injectable()
export class MoveDataService extends ObjectCache<Move> {

  constructor() {
    super();
  }

  findBySlug(slug:string): Move[] {
    return this.findAll(move => move.slug == slug);
  }

  findByName(name:string): Move[] {
    return this.findAll(move => move.name == name);
  }
}



