import { Injectable } from '@angular/core';
import { Cachable } from './ObjectCache';

export interface CharacterClass extends Cachable {
  slug: string;
  name: string;
  characterCustomizationSubjects: CharacterCustomizationSubject[];
  titleSuggestions
  startingEquipmentChoices: StartingEquipmentChoice[];

}

export interface CharacterCustomizationSubject {
  subject: string;
  customizations: ChracterCustomization[];
}

export interface ChracterCustomization {
  name: string;
  options: string[];
}

export interface StartingEquipmentChoice {
  options: string[];
}

@Injectable()
export class ClassDataService {

  constructor() { }

}



