import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Move, MoveDataService } from '../data/move-data.service';

@Component({
  selector: 'app-move-page',
  templateUrl: './move-page.component.html',
  styleUrls: ['./move-page.component.css']
})
export class MovePageComponent implements OnInit {

  move: Move;

  constructor(
    private route:ActivatedRoute,
    private moveDataService: MoveDataService) { }

  ngOnInit() {
    this.route.params.subscribe((params) => {
      this.move = this.moveDataService.get(params['moveId']);
    });
  }

}



