import { Injectable } from '@angular/core';
import { TagDataService } from './data/tag-data.service';

@Injectable()
export class InitTagsService {

  constructor(
    private tagData: TagDataService) { }

  initData() {

    this.tagData.insert({
      domain: "MOVE_TYPE",
      name: "TAG.BASEMOVE"
    });

    this.tagData.insert({
      domain: "MOVE_TYPE",
      name: "TAG.SPECIALMOVE"
    });

    this.tagData.insert({
      domain: "MOVE_TYPE",
      name: "TAG.PLAYERMOVE"
    });

    this.tagData.insert({
      domain: "MOVE_TYPE",
      name: "TAG.UNCHAINED"
    });

  }

}



