export interface Cachable {
  _id?: number;
}

export abstract class ObjectCache<T extends Cachable> {
  private autoincrement: number = 0;
  private elements: {
    [key: number]: T
  } = {};

  insert(o: T): T {
    if (o.hasOwnProperty("_id")) {
      throw `Object with id ${o._id} already exists in collection`;
    }

    o._id = this.autoincrement++;
    this.elements[o._id] = o;
    return o;
  }

  get(id: number): T {
    return this.elements[id];
  }

  findAll(filter: (o: T) => boolean): T[] {
    var resultSet = [];
    for (var id in this.elements) {
      var o = this.elements[id];
      if (filter(o)) {
        resultSet.push(o);
      }
    }
    return resultSet;
  }
}



