import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {HttpClientModule, HttpClient} from '@angular/common/http';

import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';

import { PlayerModule } from './player/player.module';
import { DataModule } from './data/data.module';
import { InitBasicMovesService } from './init-basic-moves.service';
import { SharedModule } from './shared/shared.module';
import { RouterModule } from '@angular/router';
import { MovePageComponent } from './move-page/move-page.component';
import { InitSpecialMovesService } from './init-special-moves.service';
import { InitTagsService } from './init-tags.service';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}


@NgModule({
  declarations: [
    AppComponent,
    MovePageComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
      }
    }),
    RouterModule.forRoot([], {
      useHash: true,
      enableTracing: false
    }),
    AppRoutingModule,
    PlayerModule,
    DataModule,
    SharedModule
  ],
  providers: [InitBasicMovesService, InitSpecialMovesService, InitTagsService],
  bootstrap: [AppComponent]
})
export class AppModule { }



