import { Component, OnInit } from '@angular/core';
import { Move, MoveDataService } from '../../data/move-data.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-special-moves',
  templateUrl: './special-moves.component.html',
  styleUrls: ['./special-moves.component.scss']
})
export class SpecialMovesComponent implements OnInit {

  specialMoves: Move[];

  constructor(
    private translateService:TranslateService,
    private moveDataService:MoveDataService) { }

    ngOnInit() {
      this.translateService.getTranslation(this.translateService.currentLang).subscribe(e => {
        this.specialMoves = this.moveDataService.findAll(m => {
          return m.tags.find(t => t.name == 'TAG.SPECIALMOVE') != null;
        }).sort((a,b) => {
          return this.translateService.instant(a.name) < this.translateService.instant(b.name) ? -1 : 1;
        });
      });

      this.translateService.onLangChange.subscribe(e => {
        this.translateService.getTranslation(this.translateService.currentLang).subscribe(e => {
          this.specialMoves.sort((a,b) => {
            return this.translateService.instant(a.name) < this.translateService.instant(b.name) ? -1 : 1;
          });
        });
      });
    }

}



