import { Component, OnInit, Input } from '@angular/core';
import { Move } from '../../data/move-data.service';

@Component({
  selector: 'move-component',
  templateUrl: './move-detail.component.html',
  styleUrls: ['./move-detail.component.scss']
})
export class MoveDetailComponent implements OnInit {

  @Input("move")
  move: Move;

  @Input("cardOnly")
  cardOnly: boolean = false;

  @Input("linkOnly")
  linkOnly: boolean = false;

  constructor() { }

  ngOnInit() {
  }

}



